.team-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 2rem;
  justify-items: center;
  align-items: start;
  padding: 2rem;
}

.portrait-wrapper {
  display: contents;
}

.portrait {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin: 20px;
  width: 175px;
  max-width: calc(100% - 40px);
  text-decoration: none;
}

.portrait[data-style="small"] {
  width: 100px;
}

.portrait[data-style="tiny"] {
  flex-direction: row;
  gap: 15px;
  width: unset;
  text-align: left;
}

.portrait .icon {
  position: absolute;
  left: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: calc(20px + 10%);
  aspect-ratio: 1 / 1;
  border-radius: 999px;
  background: var(--background);
  box-shadow: var(--shadow);
  transform: translate(14%, 14%);
}

.portrait[data-style="small"] .icon {
  left: -2px;
  top: -2px;
}

.portrait[data-style="tiny"] .icon {
  display: none;
}

.portrait-image {
  width: 100%;
  margin-bottom: 20px;
  aspect-ratio: 1 / 1;
  border-radius: 999px;
  object-fit: cover;
  box-shadow: var(--shadow);
}

.portrait[data-style="tiny"] .portrait-image {
  width: 50px;
  margin: 0;
}

.portrait-name {
  font-family: var(--heading);
  font-weight: var(--semi-bold);
}

.portrait-role {
  font-size: small;
  opacity: 90%;
}

.portrait-research {
  opacity: 90%;
  font-size: small;
}

.portrait[data-style="tiny"] {
  .portrait-role, 
  .portrait-research {
    display: none;
  }
}

/* --- Buttons unter dem Member --- */
.portrait-links {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-top: 0.75em;
}
